@media print {
  *,
  *::before,
  *::after {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a, a:visited {
    text-decoration: underline;
  }
  a[href^=http]::after {
    content: " <" attr(href) "> ";
  }
  a[href]::after {
    content: " (" attr(href) ")";
  }
  abbr[title]::after {
